.ui-search-group {
  background-color: #fff;
  border-radius: 8px;
  height: 52px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.ui-search-group .ui-select {
  transition: background-color 0.3s, height 0.3s;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
}
.ui-search-group .ui-select::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 1px;
  height: 80%;
  background-color: #e1e3e5;
  transform: translateY(-50%);
}
.ui-search-group .ui-select .jq-selectbox__dropdown {
  margin-top: 20px;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 0;
  padding-right: 0;
  min-width: 200px;
}
.ui-search-group .ui-select .jq-selectbox__dropdown li {
  padding: 8px 24px;
  display: flex;
  justify-content: space-between;
}
.ui-search-group .ui-select .jq-selectbox__dropdown li.sel {
  color: #70b12f;
}
.ui-search-group .ui-select .jq-selectbox__dropdown li.sel::after {
  content: "";
  background-image: url(../images/svg/check_green.svg);
  background-repeat: no-repeat;
  background-position: center right;
  background-size: 24px 24px;
  width: 24px;
  height: 24px;
}
.ui-search-group .ui-select .jq-selectbox__dropdown li:hover {
  background-color: #f0f2f3;
}
.ui-search-group .ui-select.opened {
  background-color: #e1e3e5;
}
.ui-search-group .ui-select.opened::after {
  height: 100%;
  background-color: rgba(56, 56, 56, 0.1);
}
.ui-search-group-input {
  border: none;
  outline: none;
  flex-grow: 1;
  width: 50px;
  padding-left: 20px;
  padding-right: 20px;
  transition: background-color 0.3s;
}
.ui-search-group-input:hover, .ui-search-group-input:focus {
  background-color: #f5f6f7;
  box-shadow: 0px 10px 25px rgba(22, 25, 50, 0.07);
}
.ui-search-group .ui-btn {
  height: 52px;
}
.ui-search-group.right-chbx {
  border: 1px solid #e1e3e5;
  height: 48px;
}
.ui-search-group.right-chbx .ui-select {
  height: 48px;
}
.ui-search-group.right-chbx .ui-select::after {
  display: none;
}
.ui-search-group.right-chbx .jq-selectbox__trigger-arrow {
  background-position: center;
  background-image: url(../images/svg/arrow-down.svg);
  opacity: 0.5;
  width: 20px;
  height: 20px;
}
.ui-search-group.right-chbx .jq-selectbox__select {
  padding-left: 14px;
  padding-right: 12px;
}
.ui-search-group.right-chbx .jq-selectbox__select-text {
  font-size: 16px;
  font-size: 1rem;
  font-weight: 400;
  line-height: 24px;
  color: #484949;
}
.ui-search-group.right-chbx .jq-selectbox__select-text {
  padding-right: 0;
}
.ui-search-group.right-chbx .ui-search-group-input {
  border-radius: 8px;
  padding-left: 0px;
}

